  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

a {
  color: unset;
  text-decoration: none;
}

img {
  width: 100%;
  object-fit: contain;
}

:root {
  --color-first: #B2E01E;
  --color-second: #BAB7F1;
  --color-third: #1C199A;
  --color-white: #FFF;
  --color-light: #F4F4F4;
  --color-dark: #2D2A33;
  --color-error: #E00A0D;
  --b-radius-first: 60px;
  --b-radius-second: 10px;
  --anim4: 0.4s ease-in-out;
  --anim2: 0.2s ease-in-out;
  --anim1: 0.1s ease;
  --side-offset: 60px;
  --side-offset-second: 60px;
  --top-offset: 30px;
  --top-offset-second: 60px;
  --wiggle: wiggle_arrow 5s cubic-bezier(0.52, -0.12, 0.44, 0.99) 2s infinite;
  --container-width: 1340px;
}
@media screen and (max-width: 1280px) {
  :root {
    --side-offset-second: 50px;
  }
}
@media screen and (max-width: 1023px) {
  :root {
    --side-offset: 30px;
    --b-radius-first: 40px;
    --side-offset-second: clamp(30px, -18.6348px + 10.2389vw, 60px);
  }
}

html {
  font-family: "Inter", sans-serif;
  cursor: default;
}

.title__first {
  font-family: "DaysOne";
  font-size: clamp(55px, 30.3846px + 2.4038vw, 65px);
  line-height: 1.5;
  text-transform: uppercase;
}
@media screen and (max-width: 767px) {
  .title__first {
    font-size: clamp(45px, 28.7884px + 3.413vw, 55px);
    line-height: 1;
  }
}
.title__second {
  font-family: "DaysOne";
  font-size: clamp(28px, 23.0769px + 0.4808vw, 30px);
  line-height: 1.1;
  text-transform: uppercase;
}
@media screen and (max-width: 767px) {
  .title__second {
    font-size: clamp(24px, 17.5154px + 1.3652vw, 28px);
    line-height: 1.2;
  }
}
.title__third {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.22;
}

p {
  font-size: clamp(14px, 10.7577px + 0.6826vw, 16px);
  line-height: 1.5;
}
p:not(.subtitle) + p {
  margin-top: 15px;
}

strong {
  font-weight: 700;
}

